import React, { useContext } from "react";
import { styled } from "@material-ui/core/styles";
import { TextField, InputAdornment } from "@material-ui/core";
import { UserDataContext } from "./Main";

const StyledTextField = styled(TextField)({
  "& .MuiOutlinedInput-root": {
    width: "120px",
    color: "rgba(81, 188, 147, 1)",
    margin: "auto",
    fontSize: "1.2rem",
    "& fieldset": {
      borderColor: "rgba(81, 188, 147, 0.5)",
      width: "120px",
      margin: "auto",
    },
    "&:hover fieldset": {
      borderColor: "rgba(81, 188, 147, 1)",
    },
    "&.Mui-focused fieldset": {
      borderColor: "rgba(81, 188, 147, 1)",
    },
  },
});

export default function AssessmentTextField(props) {
  const [userData, setUserData] = useContext(UserDataContext);

  const handleChange = (event) => {
    event.persist();
    props.validationRegEx.test(event.target.value) &&
      setUserData((userData) => ({
        ...userData,
        [props.name]: event.target.value,
      }));
  };

  return (
    <StyledTextField
      error={props.error}
      helperText={props.error && props.helperText}
      value={userData[props.name]}
      onChange={handleChange}
      variant="outlined"
      inputProps={{
        style: { textAlign: "center" },
        inputMode: "numeric",
      }}
      InputProps={{
        endAdornment: (
          <InputAdornment position="end">{props.adornment}</InputAdornment>
        ),
      }}
    />
  );
}
