import React, { useState, useRef } from "react";
import "./AssessmentCarousel.css";
import Stepper from "./Stepper";
import AssessmentTextField from "./AssessmentTextField";
import AssessmentCard from "./AssessmentCard";
import RadioButton from "./RadioButton";
import { Grid, Typography, Button } from "@material-ui/core";

import { CSSTransition } from "react-transition-group";

export default function AssessmentCarousel(props) {
  const transitionRef = useRef(null);

  const [currentStep, setCurrentStep] = useState(0);

  const numberOfSteps = 5;

  function stepForward() {
    currentStep < numberOfSteps - 1 && setCurrentStep(currentStep + 1);
  }

  return (
    <CSSTransition
      in={props.showResults}
      timeout={600}
      classNames="assessment-out"
      nodeRef={transitionRef}
    >
      <Grid
        className="assessment-grid"
        container
        direction="column"
        justify="center"
        alignItems="center"
        ref={transitionRef}
      >
        <div className="cardsContainer">
          {/* First Card */}

          <AssessmentCard cardStep={0} currentStep={currentStep}>
            <Typography className="cardHeader" component="h3" variant="h5">
              Specify your gender
            </Typography>

            <Grid
              className="cardOptions"
              container
              direction="column"
              justify="center"
              alignItems="center"
            >
              <RadioButton name="gender" value="male" callback={stepForward} />
              <RadioButton
                name="gender"
                value="female"
                callback={stepForward}
              />
            </Grid>
          </AssessmentCard>

          {/* Second Card */}
          <AssessmentCard cardStep={1} currentStep={currentStep}>
            <Typography className="cardHeader" component="h3" variant="h5">
              How many times do you exercise a week?
            </Typography>

            <Grid
              className="cardOptions"
              container
              direction="column"
              justify="center"
              alignItems="center"
            >
              <RadioButton
                name="activity"
                value="never"
                callback={stepForward}
              />
              <RadioButton
                name="activity"
                value="1 - 3 days"
                callback={stepForward}
              />
              <RadioButton
                name="activity"
                value="4 - 5 days"
                callback={stepForward}
              />
              <RadioButton
                name="activity"
                value="6 - 7 days"
                callback={stepForward}
              />
            </Grid>
          </AssessmentCard>

          {/* Third Card */}
          <AssessmentCard cardStep={2} currentStep={currentStep}>
            <Typography className="cardHeader" component="h3" variant="h5">
              Specify your age
            </Typography>

            <Grid
              className="cardOptions"
              container
              direction="column"
              justify="space-evenly"
              alignItems="center"
            >
              <AssessmentTextField
                adornment="years"
                name="age"
                validationRegEx={/(^\d{1,2}$|^(?![\s\S]))/}
              />
              <Button
                className="cardButton"
                variant="outlined"
                size="large"
                color="secondary"
                fullWidth
                disabled={Boolean(!props.userData.age | (currentStep !== 2))}
                onClick={() => {
                  setTimeout(stepForward, 100);
                }}
              >
                Next
              </Button>
            </Grid>
          </AssessmentCard>
          {/* Fourth Card */}
          <AssessmentCard cardStep={3} currentStep={currentStep}>
            <Typography className="cardHeader" component="h3" variant="h5">
              Specify your height
            </Typography>

            <Grid
              className="cardOptions"
              container
              direction="column"
              justify="space-evenly"
              alignItems="center"
            >
              <AssessmentTextField
                adornment="cm"
                name="height"
                validationRegEx={/(^\d{1,3}$|^(?![\s\S]))/}
                error={Boolean(
                  props.userData.height && props.userData.height > 220
                )}
                helperText="Are you so tall?"
              />

              <Button
                className="cardButton"
                variant="outlined"
                size="large"
                color="secondary"
                fullWidth
                disabled={Boolean(!props.userData.height | (currentStep !== 3))}
                onClick={() => {
                  setTimeout(stepForward, 100);
                }}
              >
                Next
              </Button>
            </Grid>
          </AssessmentCard>
          {/* Fifth Card */}
          <AssessmentCard cardStep={4} currentStep={currentStep}>
            <Typography className="cardHeader" component="h3" variant="h5">
              Specify your weight
            </Typography>

            <Grid
              className="cardOptions"
              container
              direction="column"
              justify="space-evenly"
              alignItems="center"
            >
              <AssessmentTextField
                adornment="kg"
                name="weight"
                validationRegEx={
                  /(^\d{1,3}[.]\d{1}$|^\d{1,3}[.]$|^\d{1,3}$|^(?![\s\S]))/
                }
                error={Boolean(
                  props.userData.weight && props.userData.weight > 180
                )}
                helperText="Is this really your weight?"
              />
              <Button
                className="cardButton"
                variant="outlined"
                size="large"
                color="secondary"
                fullWidth
                disabled={Boolean(!props.userData.weight | (currentStep !== 4))}
                onClick={() => {
                  setTimeout(() => {
                    props.setShowResults(true);
                  }, 100);
                }}
              >
                Next
              </Button>
            </Grid>
          </AssessmentCard>
        </div>

        <Stepper
          steps={numberOfSteps}
          currentStep={currentStep}
          setCurrentStep={setCurrentStep}
        />
      </Grid>
    </CSSTransition>
  );
}
