import React from "react";
import "./Stepper.css";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";

export default function Stepper(props) {
  function stepBack() {
    props.currentStep > 0 && props.setCurrentStep(props.currentStep - 1);
  }

  return (
    <Grid className="Stepper" container direction="row" alignItems="center">
      <Grid item container justify="flex-end" xs>
        <Button
          color="primary"
          disabled={props.currentStep === 0}
          startIcon={<ArrowBackIosIcon />}
          onClick={stepBack}
        >
          Back
        </Button>
      </Grid>

      <Grid
        item
        container
        direction="row"
        justify="center"
        alignItems="center"
        xs={5}
        sm={3}
        md={2}
      >
        {[...Array(props.steps).keys()].map((step) => {
          let stepClass =
            props.currentStep === step
              ? "step-sign step-sign-active"
              : "step-sign";
          return <div key={step} className={stepClass}></div>;
        })}
      </Grid>
      <Grid item xs></Grid>
    </Grid>
  );
}
