import React, { useRef } from "react";
import { CSSTransition } from "react-transition-group";
import "./AssessmentCard.css";

export default function AssessmentCard(props) {
  const transitionRef = useRef(null);

  const stageArr = ["inProp", "inCenterProp", "outCenterProp", "outProp"];
  let stage =
    props.currentStep <= 2 + props.cardStep
      ? stageArr[props.currentStep + 1 - props.cardStep]
      : stageArr[3];

  return (
    <>
      <CSSTransition
        in={
          stage === "inProp" ||
          stage === "inCenterProp" ||
          stage === "outCenterProp" ||
          stage === "outProp"
        }
        timeout={600}
        classNames="card-in"
        unmountOnExit
        appear
        nodeRef={transitionRef}
      >
        <CSSTransition
          in={
            stage === "inCenterProp" ||
            stage === "outCenterProp" ||
            stage === "outProp"
          }
          timeout={600}
          classNames="card-inCenter"
          appear
          nodeRef={transitionRef}
        >
          <CSSTransition
            in={stage === "outCenterProp" || stage === "outProp"}
            timeout={600}
            classNames="card-outCenter"
            nodeRef={transitionRef}
          >
            <CSSTransition
              in={stage === "outProp"}
              timeout={600}
              classNames="card-out"
              nodeRef={transitionRef}
            >
              <div className="AssessmentCard" ref={transitionRef}>
                {props.children}
              </div>
            </CSSTransition>
          </CSSTransition>
        </CSSTransition>
      </CSSTransition>
    </>
  );
}
